@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
    height: 600vh;
}
.left-obstacle {
    width:105vw;
    height: 50vh;
    position: fixed;
    top: 68vh;
    left: -30vw;
    z-index: 4;
}
.left-obstacle div {
    width: 100%;
    height: 100%;
    -webkit-transform: rotateZ(20deg);
            transform: rotateZ(20deg);
    background: linear-gradient(30deg, #D993ED 0%, #B87472 100%);
}
.right-obstacle {
    width: 70vw;
    height: 50vh;
    top: 73vh;
    right: -20vw;
    position: fixed;
    z-index: 3;
}
.right-obstacle div {
    width: 100%;
    height: 100%;
    -webkit-transform: rotateZ(-30deg);
            transform: rotateZ(-30deg);
    background: linear-gradient(30deg, #C27896 0%, #D993ED 100%);
}
.bg {
    width: 100vw;
    height: 100vh;
    position: fixed;
    background: -webkit-gradient(linear, left bottom, left top, from(#FFABF7), to(#FFF4C4));
    background: linear-gradient(0deg, #FFABF7 0%, #FFF4C4 100%);
}
.sun {
    z-index: 2;
    position: fixed;
    width: 20vw;
    height: 20vw;
    left: 50vw;
    top: 20vh;
    -webkit-transform: translate(-50%, -100vh);
            transform: translate(-50%, -100vh);
    border-radius: 100%;
    background: linear-gradient(30deg, rgb(163, 20, 63) 0%, rgb(221, 135, 64) 100%);
}
.rays {
    position: relative;
    width: 20vw;
    height: 20vw;
    opacity: 0;
    -webkit-transition: 0.5s all linear;
    transition: 0.5s all linear;
    -webkit-animation: rotation 15s infinite linear;
            animation: rotation 15s infinite linear;
}
.ray {
    z-index: 1;
    left: 50%;
    top: 50%;
    position: absolute;
    width: 3vw;
    height: 0.75vh;
    background: -webkit-gradient(linear, left bottom, left top, from(rgb(163, 20, 63)), to(rgb(221, 135, 64)));
    background: linear-gradient(0deg, rgb(163, 20, 63) 0%, rgb(221, 135, 64) 100%);
}
@-webkit-keyframes rotation {
    from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
    }
    to {
            -webkit-transform: rotate(359deg);
                    transform: rotate(359deg);
    }
}
@keyframes rotation {
    from {
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
    }
    to {
            -webkit-transform: rotate(359deg);
                    transform: rotate(359deg);
    }
}
.message {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 6;
    background: rgba(0, 0, 0, 0.384);
    color: #fff;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    opacity: 0;
    -webkit-transition: 0.5s all ease;
    transition: 0.5s all ease;
}
.message > div{
    margin-top: 50vh;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
}
.message h1, h2 {
    width: 100%;
    text-align: center;
}
.message h1 {
    font-size: 4vw;
    letter-spacing: 1vw;
}
.message h2 {
    margin-top: 2vw;
    font-size: 2vw;
    letter-spacing: 0.5vw;
}
